<script>
    // 引入vuex的辅助函数
    import {
        mapActions,
        mapState
    } from 'vuex'
    import {
        get
    } from '@/http/axios'
    import axios from "axios";
    import qs from "qs";
    export default {
        data() {
            return {
                // 轮播图数组
                swipeData: [],
                // 搜索框搜索的值
                searchValue: ''

            };
        },
        computed: {
            ...mapState('category', ['categoryData']),
            ...mapState('product', ['productData'])
        },
        filters: {
            spliceString(data) {
                return data.slice(0, 12) + '...'
            }
        },
        methods: {

            ...mapActions('category', ['getAllCategoryData']),
            ...mapActions('product', ['getAllProductData']),

            // 跳转到产品页面 并且传递参数
            toProduct(categortId, activeKey) {
                this.$router.push({
                    // 路径
                    path: '/manager/orderDetailDesc',
                    // 参数的传递
                    query: {
                        categortId,
                        activeKey
                    }
                })
            },
            // toProduct(categortId, activeKey) {
            //     // 使用编程式导航进行跳转
            //     this.$router.push({
            //         // 路径
            //         path: '/manager/product',
            //         // 参数的传递
            //         query: {
            //             categortId,
            //             activeKey
            //         }
            //     })
            // },
            // 获取所有轮播图数据
            async getAllSwipeData() {
                let res = await get('/carousel/query', {
                    status: '正常'
                })
                this.swipeData = res.data.data
            },
            // 查询产品
            queryProduct() {
                let data = {
                    page: 1,
                    pageSize: 10,
                    name: this.searchValue
                }
                if (this.searchValue == '') {
                    delete data.name
                }
                // 调用vuex中的方法 传参
                this.getAllProductData(data)
            },
            // 查询所有栏目 调用状态机中的方法
            queryCategory() {
                let data = {
                    page: 1,
                    pageSize: 100
                }
                // 调用vuex中的方法 传参
                this.getAllCategoryData(data)
            }
        },

        created() {
            this.getAllSwipeData()
            this.queryCategory()
            this.queryProduct()
        },
    };
</script>

